<template>
  <div>

    <el-form ref='form'
             :model="registerForm"
             :rules="rules">
      <el-form-item label='用户名'
                    prop="username">
        <el-input v-model="registerForm.username"></el-input>
      </el-form-item>
      <el-form-item label='密码'
                    prop="password">
        <el-input v-model="registerForm.password"
                  show-password></el-input>
      </el-form-item>
      <el-form-item label='确认密码'
                    prop='password2'>
        <el-input v-model="registerForm.password2"
                  show-password></el-input>
      </el-form-item>
      <el-form-item label='手机号'
                    prop='mobile'>
        <el-input v-model="registerForm.mobile"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click="registe">
          注册
        </el-button>
      </el-form-item>
    </el-form>
  </div>

</template>

<script>
import { postRegisterApi } from '../../http/api'
export default {
  name: 'register',
  data () {
    return {
      registerForm: {
        username: "",
        password: "",
        password2: '',
        mobile: "",
      }, rules: {
        name: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 150, message: '长度在 3 到 150 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
        ],
        password2: [
          { required: true, message: '请确认密码', trigger: 'blur' },
        ],
        mobile: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { min: 11, max: 11, message: '11个字符', trigger: 'blur' }
        ]
      }

    }
  },
  mounted () {
    console.log('123')
  },
  methods: {
    init_page () {

    },
    registe () {
      let form_data = new FormData();
      form_data.append('username', this.registerForm.username)
      form_data.append('password', this.registerForm.password)
      form_data.append('mobile', this.registerForm.mobile)
      postRegisterApi(form_data).then(
        res => {
          this.$message(res.data)
          if (res.data.errorcode == 0) {
            this.$router.push({ path: '/chart/' })
          }
        }
      )
    }
  }
}
</script>

<style>
</style>


